import React from "react";
import { Row, Col } from "react-flexbox-grid";
import Masonry from "react-masonry-css";
//Scss
import "./portfolio.scss";
//Assets
import Arrow from "../../assets/portfolio/arrow.svg";
import Preview1 from "../../assets/portfolio/project01/1.1.jpg";
import Preview2 from "../../assets/portfolio/project02/2.2.jpg";
import Preview3 from "../../assets/portfolio/project03/3.3.jpg";
import Preview4 from "../../assets/portfolio/project04/4.4.jpg";
import Preview5 from "../../assets/portfolio/project05/5.5.jpg";
import Preview6 from "../../assets/portfolio/project06/6.jpg";
import Preview7 from "../../assets/portfolio/project07/7.7.jpg";
import Preview8 from "../../assets/portfolio/project08/8.jpg";
import Preview9 from "../../assets/portfolio/project09/9.9.jpg";

//Components
import Button from "../ui-components/button/button";
import Title from "../ui-components/title/title";
import ProjectBox from "../ui-components/projectBox/projectBox";

class Portfolio extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // PORTFOLIO PROJECTS
      projects: [
        {
          id: "1",
          preview: Preview1,
          title: "IG Feed",
          tag: "Social Media",
        },
        {
          id: "2",
          preview: Preview2,
          title: "Digital Poster",
          tag: "Poster",
        },
        {
          id: "3",
          preview: Preview3,
          title: "IG Feed",
          tag: "Social Media",
        },
        {
          id: "4",
          preview: Preview4,
          title: "nicetomeetyou.site",
          tag: "Web",
        },
        {
          id: "5",
          preview: Preview5,
          title: "Business Name",
          tag: "Poster",
        },
        {
          id: "6",
          preview: Preview6,
          title: "noban.rf.gd",
          tag: "Web",
        },
        {
          id: "7",
          preview: Preview7,
          title: "Digital Poster",
          tag: "Poster",
        },
        {
          id: "8",
          preview: Preview8,
          title: "antonia.rf.gd",
          tag: "Web",
        },
        {
          id: "9",
          preview: Preview9,
          title: "IG Feed",
          tag: "Social Media",
        },
      ],
      // PORTFOLIO GALLERY WILL LOAD THIS AFTER FUNCTION "filterGallery" FINISH FILTERING
      filterResult: null,
      pickedFilter: "all",
      filterMenuActive: false,
      pickedFilterDropdown: "NEWEST"
    };
  }

  // FIRST LOAD
  componentDidMount() {
    this.filterGallery("all");
  }

  //FILTER PORTFOLIO FUNCTION
  filterGallery = (target) => {
    let projectsArr = [...this.state.projects];
    let result;

    if (target !== "all") {
      result = projectsArr.filter((project) => project.tag === target);
    } else {
      result = projectsArr;
    }

    this.setState({ filterResult: result, pickedFilter: target, pickedFilterDropdown: "NEWEST" });
  };

  // FILTER DROP DOWN HOVER MENU FUNCTION
  filterMenuHover = (event) => {
    if(event) {
      this.setState({ filterMenuActive: true });
    }else {
      this.setState({ filterMenuActive: false });
    }
  }

  // FILTER DROP DOWN HANDLER
  filterDropDownHandler = (filter) => {
    this.setState({ pickedFilterDropdown: filter, filterMenuActive: false });

    let projectsArr = [...this.state.filterResult];
    let result;

    if (filter === "NEWEST") {
      result = projectsArr.sort((a, b) => (a.id > b.id ? 1 : -1));
    }else if (filter === "OLDEST") {
      result = projectsArr.sort((a, b) => (a.id > b.id ? 1 : -1)).reverse();
    }

    this.setState({ filterResult: result});
  }

  // RENDER
  render() {
    // PORTFOLIO GALLERY RENDER
    let projectsRender = null;
    if (this.state.filterResult) {
      projectsRender = this.state.filterResult.map((project) => (
        <ProjectBox preview={project.preview} key={project.id} title={project.title} tag={project.tag} />
      ));
    }
    // PORTFOLIO GALLERY BREAKPOINTS
    const portfolioBreakpoints = {
      default: 3,
      1100: 3,
      700: 2,
      500: 1,
    };
    // PORTFOLIO FILTER DROPDOWN MENY RENDER
    let filterDroppDown = null;
    if(this.state.filterMenuActive) {
      filterDroppDown = (
        <div className="portfolio__filter-menu shadow">
          <p className="font12" onClick={() => this.filterDropDownHandler("NEWEST")}>
            NEWEST
          </p>
          <p className="font12" onClick={() => this.filterDropDownHandler("OLDEST")}>
            OLDEST
          </p>
        </div>
      );
    }

    return (
      <div id="portfolio">
        <div className="wrapper">
          <Title title="work showcase." />
          <Row>
            <Col xs={12} sm={12} md={8} lg={9}>
              <div className="portfolio__nav">
                <ul>
                  <li className={this.state.pickedFilter === "all" ? "portfolio__nav-active font12" : "font12"} onClick={() => this.filterGallery("all")}>
                    ALL
                  </li>
                  <li
                    className={this.state.pickedFilter === "Social Media" ? "portfolio__nav-active font12" : "font12"}
                    onClick={() => this.filterGallery("Social Media")}
                  >
                    SOCIAL MEDIA
                  </li>
                  <li
                    className={this.state.pickedFilter === "Web" ? "portfolio__nav-active font12" : "font12"}
                    onClick={() => this.filterGallery("Web")}
                  >
                    WEB
                  </li>
                  <li className={this.state.pickedFilter === "Poster" ? "portfolio__nav-active font12" : "font12"} onClick={() => this.filterGallery("Poster")}>
                    POSTER
                  </li>
                </ul>
              </div>
            </Col>
            <Col xs={12} sm={12} md={4} lg={3}>
              <div className="portfolio__filter" onMouseEnter={() => this.filterMenuHover(true)} onMouseLeave={() => this.filterMenuHover(false)}>
                <p>{this.state.pickedFilterDropdown} FIRST</p>
                <img src={Arrow} alt="arrow" />
                {filterDroppDown}
              </div>
            </Col>
          </Row>
          <Masonry breakpointCols={portfolioBreakpoints} className="my-masonry-grid" columnClassName="mint__gallery">
            {projectsRender}
          </Masonry>
          <p className="motion">motion design.</p>
          <Row className="video-motion">
            <Col xs={12} sm={12} md={4} lg={4}>
            <div className="video-responsive yutub">
            <iframe width="560" height="315" src="https://www.youtube.com/embed/UQamzy4_HMo" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
  </div>
            </Col>
            <Col xs={12} sm={12} md={4} lg={4}>
            <div className="video-responsive yutub">
            <iframe width="560" height="315" src="https://www.youtube.com/embed/trlE6FpqvTs" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
  </div>
              </Col>
              <Col xs={12} sm={12} md={4} lg={4}>
              <div className="video-responsive yutub">
              <iframe width="560" height="315" src="https://www.youtube.com/embed/HefZf4gzSYI" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
  </div>
              </Col>
          </Row>
          <Row className="flex-center padding40">
            <Button label="HAVE WORK FOR ME?" target={"contact"} />
          </Row>
        </div>
      </div>
    );
  }
}

export default Portfolio;
