import React from "react";
import ReactDOM from 'react-dom'
import Swiper from "react-id-swiper";
// SCSS
import "./interest.scss";
import "swiper/css/swiper.css";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faComment } from '@fortawesome/free-solid-svg-icons'


const Blog = () => (
      <div className="interest">
        <div className="blog" id="blog">
          <div className="wrapper">
            <div className="smallTitle">
              <span className="circle"></span>
              <p className="open">AVAILABLE FOR FREELANCE PROJECTS</p>
            </div>
            <h1 className="contactText">
                Looking for a designer-programmer?
                <br></br>
                Get in touch and let's chat!
            </h1>
            <h1 className="contactText1">
                Get in touch and let's chat!
            </h1>
            <div className="buttonInterested">
            <a href="https://wa.me/6289678067918"><button className="talk" size="6x"><FontAwesomeIcon icon={faComment} className="icon"/>LET'S TALK NOW</button></a>
            </div>
          </div>
        </div>
      </div>
    );


export default Blog;
