import React from "react";
import { Row, Col } from "react-flexbox-grid";
import "./contactSocial.scss";


import Instagram from "../../../assets/contact/instagram.svg";
import Linkedin from "../../../assets/contact/linkedin.svg";
import Whatsapp from "../../../assets/contact/whatsapp.svg";


const contactSocial = () => (
  <Row>
    <Col xs={12}>
      <Row center="xs">
        <Col xs={4} lg={1} className="contact__social">
          <a href="https://www.instagram.com/anthonyws28/">
            <img src={Instagram} alt="Instagram" className="instagram"/>
          </a>
        </Col>
        <Col xs={4} lg={1} className="contact__social">
          <a href="https://www.linkedin.com/in/anthony-wijaya-saputra-33730a1b8/">
            <img src={Linkedin} alt="Linkedin" />
          </a>
        </Col>
        <Col xs={4} lg={1} className="contact__social">
          <a href="https://wa.me/6289678067918">
            <img src={Whatsapp} alt="Whatsapp" />
          </a>
        </Col>
      </Row>
    </Col>
  </Row>
);

export default contactSocial;
