import React from "react";
import { Row, Col } from "react-flexbox-grid";
import "./about.scss";
// Components
import TeamBox from './teamBox';
import TeamInfo from "./teamInfo";
import TeamInfo1 from "./teamInfo 2";
import Title from "../ui-components/title/title";
// Assets
import Person01 from "../../assets/about/1.jpg";

const about = () => (
  <div id="about">
    <div className="wrapper">
      <Title title="about me." />
      <p className="sub-title">
        Hello! My name is Anthony Wijaya Saputra. I am a Mobile Applications and<br></br>Technology student at BINUS University.
      </p>

      <p className="sub-title-mobile">
        Hello! My name is Anthony Wijaya Saputra. I am a Mobile Applications and Technology student at BINUS University.
      </p>

      <Row>
        <Col md={12} lg={4}>
          <TeamBox avatar={Person01}/>
        </Col>
        <Col md={12} lg={4}>
        <TeamInfo />
        </Col>
        <Col md={12} lg={4}>
          <TeamInfo1 />
        </Col>
      </Row>
    </div>
  </div>
);

export default about;
